.rules {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 743px;
  height: 466px;
  border: 1px solid #525352;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.rules > h1 {
  font-size: 38px;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  line-height: 38px;
  font-weight: 600;
  margin-top: 40px;
  /* margin-bottom: -1px; */
}
.rules > p {
  color: #172123;
  font-size: 24px;
  text-align: center;
  line-height: 35.5px;
  letter-spacing: 0.3px;
  font-family: "Lato", sans-serif;
  font-weight: 900;
}
.rules > .safe {
  margin-bottom: -5px;
  /* margin-top: -0.5px; */
}
.rules > .safe > span {
  text-decoration: underline;
  background-color: #f4e287;
  padding: 3px;
  font-weight: 600;
}
.rules > .list > span {
  font-weight: bold;
}
.rules > img {
  width: 385px;
  height: 217px;
  margin-bottom: 30px;
}
.buttons {
  display: flex;
  gap: 400px;
  justify-content: space-between;
}
.buttons > button {
  width: 130px;
  height: 65px;
  font-size: 24px;
  font-weight: 900;
  line-height: 16px;
  font-family: "lato", sans-serif;
  color: #fff;
  background-color: #393e4d;
  border: 1px solid #dedede;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.4s;
  text-transform: uppercase;
}
.buttons > button:nth-child(1) {
  color: #222325;
  background-color: transparent;
  border: none;
  border-radius: 20px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.buttons > button:nth-child(2) {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
